    async getDetailData(type) {
      try {
        let pageSize = this.detailTable.page.pageSize;
        if (type === 'export') {
          pageSize = 99999;
        }
        const param = {
          pageNum: this.detailTable.page.currentPage,
          pageSize,
          warnType: this.currentRow.warnType,
          sortType: this.warnDurationSortType
        };
        if (this.searchData.date) {
          const newDate = this.searchData.date[1];
          newDate.setHours(23);
          newDate.setMinutes(59);
          newDate.setSeconds(59);
          param.startTime = parseTime(this.searchData.date[0]);
          param.endTime = parseTime(newDate);
        } else {
          param.startTime = getSearchToday(6) + ' 00:00:00';
          param.endTime = getSearchToday(0) + ' 23:59:59';
        }
        if (this.searchData.groupValue) {
          const str = this.searchData.groupValue[
            this.searchData.groupValue.length - 1
          ];
          if (isNaN(str)) {
            param.deviceCode = str;
          } else {
            param.groupId = str;
          }
        }
        const res = await dtWarningDetailList(param, {});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          if (type === 'export') {
            this.$refs.detailTable.exportDatax('告警明细', res.rows);
          } else {
            this.detailTable.data = res.rows;
            this.detailTable.page.total = res.total;
          }
        }
      } catch (error) {
        console.log(error);
      }
